// scss-lint:disable NestingDepth

$switch__width: rem(54);
$switch__height: rem(30);
$switch__inner-space: rem(4);

$switch__radius: $switch__height;
$switch__knob-size: $switch__height - $switch__inner-space * 2;

.switch {
  display: inline-block;
  position: relative;
  margin-bottom: rem(18);
  cursor: pointer;
  height: $switch__height;
  width: $switch__width;
  user-select: none;
  -webkit-tap-highlight-color: rgba($color__dark, 0);

  input {
    display: none;

    + label {
      @include hide-text;
      display: block;
      position: relative;
      height: $switch__height;
      width: 100%;
      cursor: pointer;
      background: $color__accent;
      border-radius: $switch__radius;
      transition: all 0.15s ease-out;
      transform: translate3d(0, 0, 0);

      &:after {
        content: '';
        display: block;
        background: $color__bright;
        position: absolute;
        top: $switch__inner-space;
        left: $switch__inner-space;
        width: $switch__knob-size;
        height: $switch__knob-size;
        border-radius: $switch__radius;
        transition: all 0.15s ease-out;
      }
    }
  }

  // HOVER STATE
  &:hover {
    label {
      background: $color__grey--dark;
    }
  }

  // CHECKED STATE
  input:checked {
    + label {
      background: $color__primary;

      &:after {
        left: $switch__width - $switch__knob-size - $switch__inner-space;
      }
    }
  }
}

// IE8 Fallback
.lt-ie9 {
  .switch {
    input {
      display: block;

      + label {
        display: none;
      }
    }
  }
}
